<template>
  <div style="width: 95%; height: 50px">
    <el-dropdown>
      <div style="padding:0px,margin:auto">
        <img
          src="https://www.baidu.com/favicon.ico"
          style="width: 50px; height: 50px; margin-top: 8px; margin-left: 10px"
        />
      </div>
      <template #dropdown>
        <el-dropdown-menu>
          <el-dropdown-item @click.native="baidu">百度</el-dropdown-item>
          <el-dropdown-item @click.native="sougou">搜狗</el-dropdown-item>
          <el-dropdown-item>谷歌</el-dropdown-item>
        </el-dropdown-menu>
      </template>
    </el-dropdown>
    <el-input
      @keyup.enter="search"
      @input="$forceUpdate()"
      v-model="input"
      autofocus
      placeholder="请输入关键字"
      style="width: 85%; height: 50px; margin: 10px"
    />
    <el-button @click.native="search" style="width: 5%; height: 50px"
      >搜索</el-button
    >
  </div>
</template>

<script>
import { ref } from 'vue'
const input = ''
let s = 1
export default {
  methods: {
    baidu() {
      return this.s = 1
    },
    sougou() {
      return this.s = 2
    },
    search() {
      let href = ''
      if (s == 1) href = 'https://www.baidu.com/s?wd=' + this.input
      else if (s == 2) href = 'https://www.sogou.com/web?query=' + this.input
      window.open(href, '_blank')
    },
  },
}
</script>
